import React, { Component } from "react";
import CodeSection from "../../CodeSection";
import styled from "styled-components";

class YoutubeVideoApi extends Component {
  componentDidMount = () => {
    window.addEventListener("scroll", this.handleScroll);
  };

  componentWillUnmount = () => {
    window.removeEventListener("scroll", this.handleScroll);
  };

  handleScroll = () => {
    const element = this.refs.element;
    const windowScrollPosition = window.pageYOffset;
    // 35為多計算margin推出去的隔間空白色區塊
    if (windowScrollPosition > element.offsetTop - 35) {
      console.log("超過了");
    } else {
      console.log("還沒到");
    }
  };

  render() {
    return (
      <div ref="element">
        <CodeSection>{`import React, { Component } from "react";
    import { Dialog, Button } from "dawn-react-ui-lib";
    
    class Example extends Component{
        state={
          open: false
        }
        // 使用者自行定義的Button的點擊事件
        openDialog = () => {
          this.setState({
            open:true
          })
        }
        // 傳下去給Dialog關閉畫面的事件
        closeDialog = () => {
          this.setState({
            open:false
          })
        }
        render(){
            return(
              <div>
                <Button
                  text="用戶確認對話框"
                  onClick={this.openDialog}
                  width="250px"
                  padding="16px"
                />
                <Dialog
                  open={this.state.open}
                  handleClose={this.closeDialog}
                  contentHeader="標題文字"
                  contentBody={<div>內容</div>}
                  userAgree
                  userAgreeFunc={() => {
                    alert("自定義確認事件");
                  }}
                />
              </div>
            )
        }
    }
    
    export default Example;`}</CodeSection>
      </div>
    );
  }
}

export default YoutubeVideoApi;
